	module.exports = {"data":{"wordpressPost":{"title":"BMJ Open: accessible medical research","content":"<p>Welcome to the <em>BMJ Open</em> blog, where we will be documenting our countdown to launch later this year.</p>\n<p><em>BMJ Open</em> is an open access journal for general medical research. Using a continuous publication model the journal will provide rapid publication for research from any medical discipline or therapeutic area.</p>\n<p>Not only will the journal publish traditional full research reports, including small or low-impact studies, but we intend to shed light on all stages of the research process by publishing study protocols, pilot studies and pre-protocols. The journal will also place great emphasis on the importance of data sharing; raw data will be linked to at its repository or hosted online as supplementary material wherever possible.</p>\n<p>This emphasis on transparency will continue with research protocols and reviewers’ comments being published alongside final papers. Authors will need to provide clear statements of their studies’ strengths and limitations. Peer review will be open, and the criteria for acceptance will be that the research was conducted in a transparent and ethical way. Naturally, all articles will need to comply with the BMJ Group’s high standards of research and publication ethics.</p>\n<p>Authors will be asked to pay article-processing charges on acceptance, although waivers will be available on request. The ability to pay will not influence editorial decisions; payment requests will be made on acceptance.</p>\n<p>Watch this space for updates on our progress – and for advance information on how to submit your research. Meanwhile, for more information you can contact the editorial office at <a href=\"mailto:editorial.bmjopen@bmjgroup.com\">editorial.bmjopen@bmjgroup.com</a>.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby for BMJ blog sites"}}},"pathContext":{"id":"73027541-7763-5d83-b88a-6db88158f828"}}

